.home_container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4em;
    user-select: none;
}

.home_container h1{
    font-size: 2.5em;
    margin-bottom: 0.5em
}

.home_container h1 span{
    color: #ffbb33 ;
    padding: 0 0.2em;
    background-color:#222;
}

.home_container p{
    margin-bottom: 1.5em;
    color: #7b7b7b;
}

.home_container img{
    width: 25vw;
    margin: 2.5em 0;
}